body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


/* Custom classes */

.background{
  color: #595858;
}

.menu-button{
  margin-left: -12;
  margin-right: 20;
}

.title{
  display: 'none',
}

.grow{
  flex-grow: 1;
}

.search{
  position: relative;
  border-radius: 10px;
  background-color: rgba(255,255,255,0.15);
  margin-right: 1em;
}

.search:hover{
  background-color: rgba(255,255,255,0.25);
}

.search-icon{
  height: 100%;
  position: absolute;
  pointer-events: none;
  display: flex;
  align-items: center;
  justify-content: center;
}
 
.input-input{
  padding: 0rem 0rem 0rem 2rem;
  transition: width 1s linear;
  width: 100%;
  color: #fff !important;
}

.main-search img{
  margin: 2rem auto;
  transition: 150ms width linear;
}

.input-flex{
  display: flex;
  align-items: center;
  justify-content: center;
}

.input-flex button{
  margin-left: 1rem;
}

.footer{
  position: fixed;
  bottom: 0;
  width: 100%;
  background-color: #355C57;
  color: white;
  padding: 0.5rem;
  box-sizing: border-box;
}

.footer small{
  font-size: 0.5rem;
}

.text-field{
  width: 40%;
}

input:-webkit-autofill {
  box-shadow: 0 0 0px 1000px white inset;
}

.text-field:focus{
  color: white;
}

.results-quantity{
  background-color: #5252c4;
  width: 90px;
  font-size: 11px;
  margin: 10px auto;
  color: white;
  padding: 0.25rem;
}

.filters-results{
  display: flex;
}

.toggle-filters{
  width: 100%;
  height: 50px;
  text-align: center;
  font-size: 18px;
  position: relative;
}

.toggle-filters:after{
  content: '';
  width: 100%;
  border-bottom: dashed 1px rgb(143, 143, 143);
  position: absolute;
  left: 0;
  top: 50%;
  z-index: 1;
}

.toggle-filters p{
  cursor: pointer;
  width: auto;
  display: inline-block;
  z-index: 3;
  padding: 0 20px 0 20px;
  position: relative;
  font-weight: lighter;
  margin: 14px;
  background-color: white;
}

.transition-test{
  transition: 200ms display linear;
}

@media(max-width: 600px){
  .title{
    display: 'block',
  } 

  .search{
    margin-left: 1rem;
    width: auto;
  }
}